    .home>div>h1 {
        border: 1px black solid;
        margin-top: 200px;
        font-family: Burgerfont;
        font-size: 55px;
        color: beige;
        text-shadow: 1px 1px 1px black;
        background: linear-gradient(0deg, rgba(255, 0, 149, 0.342), rgba(255, 0, 149, 0.418));
        display: inline-block;
        padding: 20px;
        border-radius: 20px;
        margin-bottom: 0px;
    }
    
    .home>div>h4 {
        font-family: Burgerfont;
        font-size: 30px;
        margin-top: 10px;
    }
    
    .mainChef>.chefImg {
        display: inline-block;
        background-position: 70% 10%;
        width: 390px;
        height: 400px;
        border-radius: 360px;
        background-image: url('/assets/images/head-chef.jpg');
        position: relative;
        right: 100px;
    }
    
    .mainChef>div>h5 {
        font-family: Burgerfont;
        margin-top: 0;
        font-size: 30px;
        text-shadow: 1px 1px 1px black;
        color: bisque;
    }
    
    .mainChef>div>h4 {
        color: wheat;
        text-shadow: 1px 1px 1px black;
        font-family: Burgerfont;
        margin-bottom: 10px;
        font-size: 50px;
    }
    
    hr {
        width: 50%;
    }
    
    .mainChef>p {
        font-size: 30px;
        display: inline-block;
        position: absolute;
        margin-top: 160px;
        color: wheat;
        font-family: quoteText;
        text-shadow: 1px 1px 1px black;
    }
    
    @font-face {
        font-family: quoteText;
        src: url("/assets/fonts/quoteText.ttf");
    }
    
    .mainChef {
        border: 1px black solid;
        background: linear-gradient(0deg, rgba(255, 0, 149, 0.075), rgba(255, 0, 149, 0.089));
        padding: 20px;
        border-radius: 50px;
    }
    
    @media (max-width: 1000px) {
        .mainChef>p {
            display: block;
        }
        .mainChef {
            height: 1000px;
        }
        .mainChef>.chefImg {
            right: 0;
        }
    }